@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
*{
  margin: 0;
  padding: 0;
  transition: all ease;
  scroll-behavior: smooth;
}
*::-webkit-scrollbar{
  display: none;
}
body{
  width: 100%;
  height: 100%;
  perspective: 10px;
  background-color: #AB886D;

}

.wrapper{
  width: 100%;
  height: 150vh;
  overflow-x: hidden;
  overflow-y: visible;
  perspective: 10px;
  
}


.hero{
  width: 100%;
  height: 100%;
  position: relative;
  z-index: -1;
  top: 0;
  transform-style: preserve-3d;

  
}
.hero img{
  width: 100%;
  transform: translateZ(-20px)scale(3);
  
}
.introh1 {
  top: 20%;
  left: 5%;
  transform: translateZ(-5px) scale(1.5);
  position: absolute;
  color: white;
  animation: fade-out linear both;
  animation-timeline: view();
  animation-range: entry 5% cover 90%;
}
.introh2{
  top: 50%;
  left: 28%;
  color: white;
  transform: translateZ(-5px) scale(1.5);
  position: absolute;
  animation: fade-out linear both;
  animation-timeline: view();
  animation-range: entry 5% cover 90%;

}
.introh1{
  font-size: 60px;
  font-weight: 900;
  text-shadow: 0px 6px 6px black;
  text-align: center;

}
.introh2{
  font-size: 40px;
  font-weight: 500;
  text-shadow: 0px 6px 6px black;

  
}
.box-1{
  padding: 2%;
  background-color: #493628;
  color: white;
  font-size: 20px;
  border-radius: 10px;
  margin-left: 10%;
  margin-top: 10%;
  width: 80%;
  position: relative;
  align-items: center;
  z-index: -1;
  transform: translateZ(-5px) scale(1.5);
  animation: fade linear both ;
  animation-timeline: view();
  animation-range: entry 20% cover 100%;
}
.tpc-intro{
  top: 90%;
  z-index: -1;
  width: 100%;
  display: flex;
  flex-direction: column;
  transform-style: preserve-3d;
  background-color: #AB886D;
  position:relative;
  transform-style: preserve-3d;

}
.contentntro {
  margin-top: 1%;
   width: 75%;
   height: 100%;
   margin-left: 12.5%;
   margin-bottom: 20%;
   background-color: #493628;
   display: flex;
  flex-direction: row;
  border: none;
  border-left: 5px solid antiquewhite ;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.585);
  border-bottom: 5px solid antiquewhite ;

}

.tp-intro{
  color: antiquewhite;
  font-size: 25px;
  width: 100%;
  height: 100%;
  margin: 2%;

}
.tp-intro h1{
  font-size: 60px;
  font-weight: 800;
  width: 100%;
  margin-bottom: 1%;
  animation: slide-in linear both;
  animation-timeline: view();
  animation-range: entry 20% cover 60%;
}
.tp-intro h3{
  width: 100%;
  font-size: 30px;
  font-weight: 400;
  margin-bottom: 4%;
}
.tp-intro p{
  font-size: 20px;
  width: 40%;
}

.link-dis{
  width: 30%;
