.subtp{
  width: 100%;
  height: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-top: 5%;
  padding-bottom: 5%;
  padding-left: 1%;
  position: fixed;
  transform: translate(0%, 150%);
  
}
.subtp-container{
  width: 8%;
  height: 80%;
  overflow: hidden;
  margin-right: 2%;
  transition: all ease 0.5s;
}
.subtp-container img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.subtp-container:hover {
  width: 50%;
}
.subtp-container:hover h1{
  transform: translate(1%, -100%);
  
}

.subtp-container h1{
  font-size: 40px;
  font-weight: 900;
  text-shadow: 0px 2px 2px black;
  transform: translate(1%, -1050%);
  color: antiquewhite;
  transition: all ease 0.5s;

}
.subtp-container h3{
  font-size: 30px;
  font-weight: 400;
  transform: translate(1%, -100%);
  color: antiquewhite;
  transition: all ease 0.5s;

}
.cards {
  width: 100%;
  height: 50%;
  transform: translate(0%, -240%);
	list-style: none;
	white-space: nowrap;
	overflow-x: scroll;
}

.topic-card {
  width: 50%;
