#one , #two, #five{
  transform: translate(0%, -190%);
}
#three{
  transform: translate(0%, -170%);
}
#four{
  transform: translate(0%, -190%);
}

/* animations */

@keyframes fade-out {
  0%{
    opacity: 0;
  
  }
  50%{
    opacity: 1;
  }
  80%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
  
}
@keyframes fade {
  0%{
    opacity: 0;
    
  }
  50%{
    opacity: 1;
  }
  80%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
  
}
@keyframes pops-in {
  0%{
   transform:translate(110%, 50%) scale(0);
  opacity: 0;
  }
  50%{
    transform:translate(110%, 50%) scale(0.5);
    opacity: 1;
  }
  100%{
    transform: translate(110%, 50%) scale(1);
    opacity: 1;

  }
  
}
@keyframes slide-in {
  0%{
    transform: translateX(-150px);
    opacity: 0;
  }
  100%{
    transform: translateX(0px);
    opacity: 1;
  }
}
@keyframes adjust-image {
	0% {
		object-position: 100% 0;
	}
	100% {
		object-position: 0 0;
	}
}
@keyframes slideshow {
  0%{
    transform:translateX(1px) scale(0.5);
    opacity: 0;
  }
  50%{
    transform: translateX(0px) scale(1);
    opacity: 1;
  }
  100%{
    transform: scale(0.5);
    opacity: 0;
  }
  
}